















































































































.date-picker-wrap{
  .label{
    font-weight: bolder;
    color: #999999;
  }
  .date-picker{
    margin: 5px;
  }
  .text{
    cursor: pointer;
  }
  i{
    font-size: 16px;
    color: #9d9d9d;
    font-weight: 900;
  }
  .arrow-visible{
    color: #409eff;
  }
}

